import { IconButton } from "@material-ui/core";
import { Delete } from "@material-ui/icons";
import { memo, useContext } from "react";
import AppContext from "../../../context/Context";
import PropTypes from "prop-types";
import { MEALS, WEEKDAYS } from "../../../constants/Constants";
import { deleteID, getIndexFromID } from "../../../helper/UniqueId";
const CustomListItemDeleteButton = memo(function CustomListItemDeleteButton({
  day,
  meal,
  listIndHash,
  setData,
}) {
  const listInd = getIndexFromID(day, meal, listIndHash);

  function deleteItem(index) {
    setData((state) => {
      if (
        state &&
        state[day] &&
        state[day][meal] &&
        state[day][meal].length &&
        index >= 0 &&
        state[day][meal].length > index
      ) {
        const array = state[day][meal];
        deleteID(day, meal, index);
        return {
          ...state,
          [day]: {
            ...state[day],
            [meal]: array.slice(0, index).concat(array.slice(index + 1)),
          },
        };
      }
      return state;
    });
  }

  return (
    <IconButton
      onClick={(e) => {
        deleteItem(listInd);
      }}
    >
      <Delete fontSize="small" />
    </IconButton>
  );
});

CustomListItemDeleteButton.propTypes = {
  setData: PropTypes.func.isRequired,
  listIndHash: PropTypes.number.isRequired,
  meal: PropTypes.oneOf(MEALS).isRequired,
  day: PropTypes.oneOf(WEEKDAYS).isRequired,
};

const CustomListItemDeleteButtonContext = function CustomListItemDeleteButtonContext({
  listIndHash,
  day,
  meal,
}) {
  const context = useContext(AppContext);

  if (context && context.setDataUnConstraint) {
    return (
      <CustomListItemDeleteButton
        listIndHash={listIndHash}
        day={day}
        meal={meal}
        setData={context.setDataUnConstraint}
      />
    );
  }

  return <></>;
};

CustomListItemDeleteButtonContext.propTypes = {
  listIndHash: PropTypes.number.isRequired,
  meal: PropTypes.oneOf(MEALS).isRequired,
  day: PropTypes.oneOf(WEEKDAYS).isRequired,
};

export default CustomListItemDeleteButtonContext;
